*
{
    margin: 0;
    padding: 0;
    
}

section{
    position: relative;
    width: 100%;
    height: 100vh;
    background: linear-gradient(to top,#adacac 0%, #f0f1f1 100%);
}
section h2 
{
    position: relative;
    width: 100%;
    height: 100vh;
    text-align: center;
    line-height: 100vh;
    font-size:10vw;
    color: black;
    font-weight:700 ;
}

.banner
{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: flex;
    flex-wrap: wrap ;
}
.banner .blocks 
{
    position: relative;
    display: block;
    width: 5.2631%;
    height:5vw;
    animation: animate 0.75s ease-in-out forwards;
}
.banner .blocks:nth-child(even){
    animation: animate 1s ease-in-out forwards;
}
.banner .blocks:nth-child(7n+3){
    animation: animate 2.5s ease-in-out forwards;
}
.banner .blocks:nth-child(7n+7){
    animation: animate 1.5s ease-in-out forwards;
}
@keyframes animate 
{
    0%{
        opacity: 0;
        transform: scale(0) translateY(1000px);
        
    }
    50%{
        opacity: 1;
        background: url(https://149367133.v2.pressablecdn.com/wp-content/uploads/2020/08/sony-wh-1000xm4-gadgetmatch-20200807-02.jpg);
        background-position: center;
        background-attachment: fixed;
        background-size: cover;
       
    
    }
    100%{
        opacity: 1;
        transform: scale(1) translateY(0px);
        background: url(https://149367133.v2.pressablecdn.com/wp-content/uploads/2020/08/sony-wh-1000xm4-gadgetmatch-20200807-02.jpg);
        background-position: center;
        background-attachment: fixed;
        background-size: cover;
       
    }
}
